<template>
    <div>
      <!-- 查询 -->
      <common-wrapper>
        <main-management-search class="search" :warehouseList="warehouseList" @search="queryList" @rest="rest" />
      </common-wrapper>

      <!-- 按钮 -->
      <nav-button>
        <el-button-group>
          <el-button icon="el-icon-download" >导出明细</el-button>
        </el-button-group>
      </nav-button>
     <!-- 用户列表 -->
      <common-wrapper>
        <common-table :flex="230">
          <template slot="table">
            <el-table
              ref="userTable"
              height="100%"
              stripe
              v-loading="isListLoading"
              :data="tableData"
              @row-click="getUserDetail"
              @selection-change="handleSelectionChange">
                <el-table-column sortable prop="name" label="姓名" width="120"></el-table-column>
                <el-table-column sortable prop="date" label="日期" width="120"></el-table-column>
                <el-table-column sortable prop="unload" label="卸货" width="120" ></el-table-column>
                <el-table-column sortable prop="putaway" label="上架" width="80"></el-table-column>
                <el-table-column sortable prop="stockup" label="备货" width="120"></el-table-column>
                <el-table-column sortable prop="send" label="发货"></el-table-column>
            </el-table>
          </template>
          <template slot="pagination">
                <el-pagination   background   layout="total, prev, pager, next"
                :current-page.sync="pagination.pageNum"
                :page-size="pagination.pageSize"
                :total="pagination.total"
                @current-change="changePage"
                ></el-pagination>
          </template>
        </common-table>
      </common-wrapper>
    </div>
</template>

<script>
/**
 * operationDetail Management Module
 * ----------------------
 * Author: Calamus
 * Date: 2018.09.05
 */
import moment from 'moment'
// 全局混入
import {mixins} from '@/mixins'
// 自定义组件
import NavButton from '@/components/navButton'
import NavPremissonButton from '@/components/navButton/navPremissonButton'
import CommonWrapper from '@/components/commonWrapper'
import CommonTable from '@/components/commonTable'
import MainManagementSearch from './Components/Search'
// API
import { operationDetailList } from '@/api/datareport'


export default {
  name: 'operationDetail',
  mixins: [mixins],
  components: {
    NavButton,
    NavPremissonButton,
    CommonWrapper,
    CommonTable,
    MainManagementSearch
  },
  data () {
    return {
      comp: this,
      buttonData: [],
      tableData: [],
      selectRow: [], // 列表选中列
      choosedIds:[],
      isListLoading: false, // 列表加载
      warehouseList: this.$store.getters.warehouseList,
      isCreateFormVisible: false, // 新增界面是否显示
      isEditFormVisible: false, // 编辑界面是否显示
      isRoleAuthVisible: false, // 用户角色授权窗口是否展示
      modifyUser: null, // 弹出框编辑时存放的临时行数据
      pagination: { // 翻页数据
        pageNum: 1,
        pageSize: 10,
        total: 0
      },
      searchParams: {} // 查询参数
    }
  },
  activated () {
    this.$refs.userTable.doLayout()
  },
  methods: {
    // 查询和初始化查询
    // 查询/翻页公用方法
    queryList (params) {
      const searchParams = this.searchParams = params
      this.getList(
        {
          ...searchParams,
          pageNum: this.pagination.pageNum,
          pageSize: this.pagination.pageSize
        }
      )
    },
    // 获取列表
    getList (params) {
      this.isListLoading = true
      operationDetailList(params).then(res => {
        this.tableData = res.list
        this.pagination.total = res.total;
        this.pagination.pageNum = res.pageNum
        this.pagination.pageSize = res.pageSize
        this.isListLoading = false
      })
    },
    rest (params) {
      const searchParams = this.searchParams = params
      this.getList(
        {
          ...searchParams,
          pageNum: 1,
          pageSize: this.pagination.pageSize
        }
      )
    },
    handleSelectionChange(item){
      this.selectRow = item ;
      let oIds = [] ;
      this.choosedIds = this.selectRow.map(row => oIds.push(row.id)).join(',');
    },
    isButtonIsDisabled () {
      return !!this.selectRow.length
    },
    isButtonDisabled () {
      return !!this.selectRow.length
    },
    // 查询详情
    getUserDetail (row, event) {
      // console.log(row)
    },
    // 列表翻页
    changePage (pageNum) {
      this.pagination.pageNum = pageNum
    },
    // 生成列表序号
    indexMethod (index) {
      return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
    },
    // 列表状态显示格式化
    formatStatus (row, column) {
      return row.userEnable === '1' ? '启用' : row.userEnable === '0' ? '禁用' : '未知'
    },
    // 列表日期格式化
    formatCreateDate (row) {
      return moment(row.userCreateTime).utcOffset(8).format('YYYY-MM-DD')
    }
  }
}
</script>



